import React from "react";
import Card from "../components/Card";
import image from "../assets/images/pp.jpg";
import image2 from "../assets/images/pp2.JPG";
const Gallery = () => {
  return (
    <>
      <div style={{ backgroundColor:'#c2e0e0' }} className="gallery vh-100">
      <div className="container">
        <div className="row">
          <div className="col-4">
          <Card
            image={image}
            work="Fullstack Developer"
            name="Yogi Efani"
            desk="ia adalah seorang fullstack di perusahaan example company"
            umur="19"
            alamat="Jakarta - Indonesia"
          />
          </div>
          <div className="col-4">
          <Card
            image={image2}
            work="Frontend Developer"
            name="Yancandra"
            desk="ia adalah seorang frontend developer di perusahaan example company"
            umur="20"
            alamat="Yogyakarta - Indonesia"
          />
          </div>
          <div className="col-4">
          <Card
            image={image2}
            work="Frontend Developer"
            name="Yancandra"
            desk="ia adalah seorang frontend developer di perusahaan example company"
            umur="20"
            alamat="Yogyakarta - Indonesia"
          />
          </div>
        </div>
      </div>
      </div>
    </>
  );
};

export default Gallery;
