import React from 'react'

function Card({image,work,name,desk,umur ,alamat}) {
  return (
    <>
    <div className=' w-100 d-flex justify-content-center mt-5 ' data-aos="fade-up">
    <div className='card mt-5' style={{ width: '18rem' }}>
        <div className='w-100 d-flex justify-content-center'>
        <img style={{ borderRadius:'50%', width:80 }} src={`${image}`} className=' card-img-top mt-3 mx-3' alt="image" />
        </div>
        <div className='card-body'>
            <span 
            className='text-muted'>{umur} tahun</span>
            <h6 style={{ color:'#044750' }}>{work}</h6>
            <h5 
            style={{ color:'#494949',marginTop:'-5px' }}
            className='card-title'>{name}</h5>
            <p className='card-text'>{desk}</p>
        </div>
        <ul className='list-group list-group-flush'>
            <li className='list-group-item text-muted'>Alamat : {alamat} </li>
        </ul>
        <div className='card-body d-flex justify-content-center'>
            <a data-toggle="modal" data-target="#detail-profile" className='card-link btn btn-success'>Detail</a>
        </div>
    </div>
    </div>
    </>
  )
}

export default Card
