import React from "react";
import { Link } from 'react-router-dom';

const Home = () => {
  return (
    <>
    <div style={{ backgroundColor:'#c2e0e0' }} className="home vh-100">
      <div className="container pt-5 mt-5 " data-aos="fade-down">
        <div class="jumbotron">
          <h1 class="display-4">Hello, Mahasiswa Baru!</h1>
          <p class="lead">
            Ayo Join, Apps Amikom 3. Aplikasi ini mempermudah kalian untuk
            belajar
          </p>
          <hr class="my-4" />
          <p>
            It uses utility classes for typography and spacing to space content
            out within the larger container.
          </p>
          <div className="d-flex w-100 justify-content-center">
          <Link to={"/Login"} class="btn btn-primary btn-lg" href="#" role="button">
            Login
          </Link>
          </div>
        </div>
      </div>
    </div>
    </>
  );
};

export default Home;
